import React from 'react';
import ReactDOM from 'react-dom';
import { Route, BrowserRouter as Router, Switch, Link } from 'react-router-dom';

import "./main.css"

const SECTIONS = [
    "MakerOfGames",
    "ExtendedReality"
]

class Main extends React.Component
{
    render()
    {
        return (
            <Router>
                <div className="TopBar">
                    <ul>
                        <li><Link to="/about">Vivek Raman</Link></li>
                        <li><Link to="/blog">Blog</Link></li>
                        <li><Link to="/work">Work</Link></li>
                        <li><Link to="/contact">Contact</Link></li>
                    </ul>
                </div>
                
                <Switch>
                    <Route path="/about">
                        <LandingPage />
                    </Route>
                    <Route path="/blog">
                        <Blog />
                    </Route>
                    <Route path="/">
                        <LandingPage />
                    </Route>
                </Switch>
            </Router>
        );
    }
}

class Blog extends React.Component
{
    render()
    {
        return (
            <div>blog</div>
        );
    }
}

class LandingPage extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            cycleCount: 0
        };
        
        setInterval(() => {
            this.setState(
                {cycleCount: (this.state.cycleCount + 1) % SECTIONS.length}
            );
            this.CycleModes();
        }, 5000);

        this.SetBodyClass(SECTIONS[this.state.cycleCount]);
    }

    CycleModes()
    {
        
        this.SetBodyClass(SECTIONS[this.state.cycleCount]);
    }

    SetActiveBigText(sectionName)
    {

    }

    SetBodyClass(sectionName)
    {
        document.querySelector("body").classList = [];
        document.querySelector("body").classList.add(sectionName);
    }

    render()
    {
        let makerOfGames = <li className={SECTIONS[0]} onClick={() => this.MakerOfGames()}>Maker of Games</li>
        let extendedReality = <li className={SECTIONS[1]} onClick={() => this.ExtendedReality()}>Extended Reality</li>
        return (
            <div className="BigText">
            <ul>
                {makerOfGames}
                {extendedReality}
            </ul>
            </div>
        );
    }
}
 
ReactDOM.render(
    <Main />, document.getElementById('root')
);
